        <el-aside width="200px">
          <div class="img">
            <img src="../assets/pic.jpg" alt="头像">
            <p style="color:#999;font-size:12px;">在职-考虑机会</p>
          </div>
          <div class="basic-info">
            <div class="info-item">
              <i class="iconfont icon-xingming"></i>
              <span>吴罡皇</span>
              <i class="iconfont icon-nan"></i>
            </div>
            <div class="info-item">
              <i class="iconfont icon-gongzuoshijian"></i>
              <span>2016年7月起</span>
            </div>
            <div class="info-item">
              <i class="iconfont icon-youxiang"></i>
              <span>815600709@qq.com</span>
            </div>
            <div class="info-item">
              <i class="iconfont icon-dianhua"></i>
              <span>+86 13631469158</span>
            </div>
          </div>
          <div class="menu">
            <div :class="['menu-item', hotIndex === 0 ?'menu-hover':'']" @click="linkTo(0, '/main/brief')">个人简介<span class='inner'></span></div>
            <div :class="['menu-item', hotIndex === 1 ?'menu-hover':'']" @click="linkTo(1, '/main/technology')">技术能力<span class='inner'></span></div>
            <div :class="['menu-item', hotIndex === 2 ?'menu-hover':'']" @click="linkTo(2, '/main/job')">求职意向<span class='inner'></span></div>
            <div :class="['menu-item', hotIndex === 3 ?'menu-hover':'']" @click="linkTo(3, '/main/working')">工作经历<span class='inner'></span></div>
            <div :class="['menu-item', hotIndex === 4 ?'menu-hover':'']" @click="linkTo(4, '/main/suffer')">创业经历<span class='inner'></span></div>
            <div :class="['menu-item', hotIndex === 5 ?'menu-hover':'']" @click="linkTo(5, '/main/project')">项目经验<span class='inner'></span></div>
            <div :class="['menu-item', hotIndex === 6 ?'menu-hover':'']" @click="linkTo(6, '/main/aboutme')">自我介绍<span class='inner'></span></div>
          </div>
        </el-aside>
        <el-main :style="{'height': height + 'px'}">
          <el-collapse-transition>
            <Brief v-if="hotIndex === 0"></Brief>
            <Technology v-if="hotIndex === 1"></Technology>
            <Job v-if="hotIndex === 2"></Job>
            <Work v-if="hotIndex === 3"></Work>
            <Suffer v-if="hotIndex === 4"></Suffer>
            <Project v-if="hotIndex === 5"></Project>
            <AboutMe v-if="hotIndex === 6"></AboutMe>
          </el-collapse-transition>
        </el-main>
