</template>

<script>
  import Brief from './brief.vue'
  import Technology from './technology.vue'
  import Job from './jobDream.vue'
  import Work from './working.vue'
  import Suffer from './suffer.vue'
  import Project from './project.vue'
  import AboutMe from './aboutme.vue'
  import Config from './config.js'
  export default {
    name: 'resume',
    data () {
      return {
        hotIndex: 0,
        height: 625
      }
    },
    methods: {
      linkTo (index, url) {
        this.hotIndex = + index
        var query = this.$route.query
        if (query && Object.keys(query).length > 0) {
          this.$router.push({path: url, query: query})
        }
      },
      download () {
        var a = document.createElement('a')
        a.href = 'resume.rar'
        a.download = '简历.rar'
        document.body.append(a)
        a.click()
        document.body.removeChild(a)
      }
    },
    components: {
      Brief,
      Technology,
      Job,
      Work,
      Suffer,
      Project,
      AboutMe
    },
    mounted () {
      console.log(1111)
      this.$nextTick(() => {
        this.height = document.documentElement.clientHeight - 160
        console.log(this.height)
      })
      var path = this.$route.path
      switch (path) {
        case '/main':
          this.hotIndex = 0
          break;
        case '/main/brief':
          this.hotIndex = 0
          break;
        case '/main/technology': 
          this.hotIndex = 1
          break;
        case '/main/job':
          this.hotIndex = 2
          break;   
        case '/main/working':
          this.hotIndex = 3
          break;
        case '/main/suffer':
          this.hotIndex = 4
          break;  
        case '/main/project':
          this.hotIndex = 5
          break;  
        case '/main/aboutme':
          this.hotIndex = 6
          break;
        default:
          this.$router.push({path: '/'})
      }
      var query = this.$route.query
      if (query && query.code === Config.key) {
        console.log('校验成功')
      } else {
        this.$router.replace({path: '/'})
      }
    }
  }

</script>

