<template>
    <div class="code">
        <div class="wrapper">
            该网站内容涉及到隐私信息, 请输入授权码确认
            <el-input v-model="code" @keyup.enter="commit"></el-input>
            <div style="text-align: right">
                <el-button @click="commit" type="primary">校验</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import keyCode from './config.js'
    export default {
        name: 'codes',
        data() {
            return {
                code: ''
            }
        },
        methods: {
            commit() {
               if (this.code === keyCode.key) {
                   this.$router.replace({path: '/main?code=' + this.code})
               } else {
                   this.$message.error('请输入正确的授权码');
               }
            }
        }
    }

</script>

<style lang="less" scoped>
    .code {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: #dadada;
        .wrapper{
            background-color:#fff;
            padding: 20px;
            border-radius: 4px;
            width: 400px;
            position:absolute;
            left: 50%;
            top: 50%;
            transform: translateX(-50%) translateY(-50%);
            .el-input{
                margin: 20px 0;
            }
        }
    }
</style>